.wrapper {
  width: 100%;
  height: 100%;
  min-height: 100dvh;
  padding: 0 15px;
  background: #131518;
  color: #fff;
  box-sizing: border-box;
}
.wrapper h1 {
  margin: 0;
  padding: 20px 0;
}
.list_movie {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}
.list_movie .movie {
  width: calc((100% - 70px) / 8);
  font-size: 16px;
}
.list_movie .movie a {
  color: #fff;
  display: block;
  text-decoration: none;
}
.list_movie .movie .img {
  width: 100%;
}
.list_movie .movie .tit {
  font-size: 16px;
  margin: 10px 0;
}
.list_movie .movie .summary {
  margin: 0;
  font-size: 14px;
  -webkit-line-clamp: 4;
  text-overflow: ellipsis;
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
}
.list_movie .movie .genres {
  display: flex;
  gap: 5px;
  flex-wrap: wrap;
  margin: 15px 0;
  padding: 0;
}
.list_movie .movie .genres li {
  font-size: 11px;
  list-style: none;
  background-color: #ddd;
  color: #131518;
  font-weight: 700;
  display: inline-block;
  padding: 4px 8px;
  line-height: 14px;
  border-radius: 11px;
}

@media all and (max-width: 1280px) {
  .list_movie .movie {
    width: calc((100% - 40px) / 5);
  }
}
@media all and (max-width: 768px) {
  .list_movie .movie {
    width: calc((100% - 30px) / 4);
  }
}
@media all and (max-width: 640px) {
  .list_movie .movie {
    width: calc((100% - 20px) / 3);
  }
}
@media all and (max-width: 375px) {
  .list_movie .movie {
    width: calc((100% - 10px) / 2);
  }
}
@media all and (max-width: 320px) {
  .wrapper {
    padding: 0 30px;
  }
  .list_movie .movie {
    width: 100%;
  }
}
