.detail {
  width: 100%;
  height: 100%;
  min-height: 100dvh;
  padding: 0 15px;
  background: #131518;
  color: #fff;
  box-sizing: border-box;
}
.detail h1 {
  margin: 0;
  padding: 20px 0;
}
.info {
  display: flex;
  gap: 15px;
}
.info .img {
  width: 25%;
  max-width: 300px;
  min-width: 140px;
}
.info .img img {
  width: 100%;
}
.info ul {
  flex-grow: 1;
  margin: 0;
  padding: 0;
}
.info ul li {
  margin: 0;
  padding: 10px 0 10px 20px;
  list-style: none;
  position: relative;
  line-height: 24px;
}
.info > ul > li::before {
  content: "";
  position: absolute;
  top: 20px;
  left: 5px;
  width: 4px;
  height: 4px;
  background-color: #fff;
  border-radius: 50%;
}
.info ul .tit {
}
.info ul .release {
}
.info ul .runtime {
}
.info ul .genres,
.info ul .genres ul {
  display: flex;
  gap: 5px;
  flex-wrap: wrap;
}
.info ul .genres li {
  font-size: 11px;
  list-style: none;
  background-color: #ddd;
  color: #131518;
  font-weight: 700;
  display: inline-block;
  padding: 4px 8px;
  line-height: 14px;
  border-radius: 11px;
}
.info ul .desc {
  max-width: 600px;
}
